/* Header container */
.header {
  padding: 20px;
  background-color: #FFFFFF;
}

/* Flexbox styling to center content */
.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
}

/* Font styling for titles */
.header-title, .footer-title {
  font-family: 'FatCat', sans-serif;
  font-size: 20px;
  font-weight: bold;
  color: #00473e;
}

/* Logo styling */
.logo {
  width: 120px;
  margin-left: auto;
  margin-right: auto;
}

/* Navigation styling */
.nav-links {
  display: flex;
  justify-content: center;
  gap: 40px;
}

.nav-link {
  font-size: 18px;
  font-weight: bold;
  color: #00473e;
  text-decoration: none;
  background: none;
  border: none;
  cursor: pointer;
}

.nav-link:hover {
  color: #3399cc;
}

/* Join subscription box */
.join-box {
  border-radius: 50px;
  padding: 30px;
  background-color: #FFFFFF;
  width: 25%;
  margin: 0 auto;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

/* Footer title styling */
.footer-title {
  margin-top: 40px;
  border-top: 1px solid #003b00;
  padding-top: 20px;
}

/* Responsive styles */
@media (max-width: 1024px) {
  .join-box {
    width: 40%;
  }

  .nav-links {
    gap: 30px;
  }
}

@media (max-width: 768px) {
  .logo {
    width: 100px;
  }

  .join-box {
    width: 60%;
  }

  .nav-links {
    gap: 20px;
  }
}

@media (max-width: 480px) {
  .join-box {
    width: 80%;
  }

  .nav-links {
    flex-direction: column;
    gap: 10px;
  }
}
