.footer {
    background-color: #00473e;
    color: white;
    padding: 20px 0;
    text-align: center;
  }
  
  .footer-content {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    padding: 20px;
    max-width: 1200px;
    margin: 0 auto;
  }
  
  .footer h3 {
    font-size: 18px;
    margin-bottom: 10px;
  }
  
  .footer ul {
    list-style: none;
    padding: 0;
  }
  
  .footer ul li {
    margin: 5px 0;
  }
  
  .footer ul li button {
    background: none;
    border: none;
    color: #FAAE2B;
    cursor: pointer;
    text-decoration: underline;
  }
  
  .footer ul li button:hover {
    color: #FFFF0F;
  }
  
  .footer-bottom {
    margin-top: 20px;
    font-size: 14px;
  }
  
  .contact a {
    color: #FAAE2B;
  }
  
  .contact a:hover {
    color: #FFFF0F;
  }
  
  .policy-modal {
    width: 80%;
    margin: 100px auto;
    background: white;
    padding: 20px;
    border-radius: 10px;
    position: relative;
  }
  
  .modal-content {
    padding: 20px;
    max-height: 500px;
    overflow-y: auto;
  }
  
  .close-modal {
    position: absolute;
    top: 10px;
    right: 20px;
    background-color: transparent;
    border: none;
    font-size: 20px;
    cursor: pointer;
  }
  
  .policy-text {
    font-family: Arial, sans-serif;
    font-size: 14px;
    line-height: 1.6;
  }
  
  .policy-modal-overlay {
    background: rgba(0, 0, 0, 0.75);
  }
  